@use "../../styles/partials/mixins" as *;
@use "../../styles/partials/variables" as *;
@use "../../styles/global/global" as *;

.dashboard {
  display: flex;
  justify-content: space-between;
  background-color: #ffffff;
  height: 100%;
  width: 100%;

  &__main-content {
    background-color: $White;
  }

  &__container {
    display: flex;
    justify-content: space-between;
    width: 100%;
    padding: 1.75rem 2.25rem;
  }
}
